<template>
    <div>
        <!-- {{localItems.map(i=>i.id)}} -->
        <el-collapse v-model="activeNames">
            <el-collapse-item title="发送者-接收者-总线"
                              :name="2">
                <sender></sender>
                <recevier></recevier>
            </el-collapse-item>
            <el-collapse-item title="接收者--简单store --定义一个全局的响应式变量"
                              :name="3">
                <el-row>
                    <el-col :span='12'>
                        <child-b></child-b>
                    </el-col>
                    <el-col :span='12'>
                        <child-a></child-a>
                    </el-col>
                </el-row>

            </el-collapse-item>

        </el-collapse>

    </div>
</template>

<script>
import childA from './childA';
import childB from './childB';
import sender from './sender';
import recevier from './recevier';
export default {
    data() {
        return {
            activeNames: [0, 1, 2, 3, 4]
        };
    },

    components: {
        sender,
        recevier,
        childA,
        childB
    }
};
</script>




