<template>
    <div>

        <el-form label-width="80px">
            <el-row>
                <el-col :span="6">
                    <el-input v-model="msg2Post"></el-input>
                </el-col>
                <el-col :span="6">
                    <el-button @click="postMsg">发送消息</el-button>
                </el-col>
                <el-col :span="6">
                    <el-input v-model="msg2get"></el-input>
                </el-col>
                <el-col :span="6">
                    <el-button @click="getMsg">接收消息</el-button>
                </el-col>
            </el-row>
        </el-form>

    </div>
</template>

<script>
export default {
    data() {
        return {
            activeNames: [0, 1, 2, 3, 4],
            msg2Post: '',
            msg2get: ''
        };
    },
    created() {
        this.$bus.$on('2222', msg => {
            this.msg2get = msg;
        });
    },
    methods: {
        postMsg() {
            this.$bus.$emit('111', this.msg2Post);
        },
        getMsg() {
            // this.msg2get = this.$getMsg();
        }
    },
    components: {}
};
</script>




