const state = {
    test1: [],
    test2: 'o'
}
const getters = {
    test3(state, getters, rootState) {
        let t1 = state.test1.concat(rootState.products)
        return {
            t1,
            t2: state.test2
        }
    }
}
export default {
    state,
    getters
}


