import Vue from "vue";

class LocalStore {
    constructor(Vue, options) {
        // var bus = new Vue({
        //         data: {
        //             state: options.state
        //         }
        //     })
        // this.install(Vue, bus)
        this.install(Vue, options)
    }
    install(Vue, bus) {
        Vue.mixin({
            beforeCreate() {
                Vue.util.defineReactive(this, '$localStore', bus);
            }
        })

        // Vue.prototype.$localStore = bus
        // Vue.prototype.$localStore = bus
        // Vue.mixin({
        //     beforeCreate() {
        //         if (this.$options.localStore) {
        //             Vue.prototype.$localStore = bus
        //         }
        //     }
        // })
    }
}

const localStore = new LocalStore(Vue, {
    state: {
        count: 0
    }
})

export default localStore


