import Vue from 'vue'
// import Vue from 'vue/dist/vue.runtime.common.js'
// import Vue from 'vue/src/core/instance/index.js'

import VueRouter from "vue-router";

// 引入组件

import keyTest from "../pages/keyTest"
import vuexTest from "../pages/vuexTest"
import msgObjTest from "../pages/msgObjTest"
// 要告诉 vue 使用 vueRouter
Vue.use(VueRouter);

const routes = [{
        path: "/",
        redirect: '/keyTest'
    },
    {
        name: ":key测试",
        path: "/keyTest",
        component: keyTest
    }, {
        name: "vuex测试",
        path: "/vuexTest",
        component: vuexTest
    }, {
        name: "msg测试",
        path: "/msgObjTest",
        component: msgObjTest
    }


]


var Router = new VueRouter({
    routes,
    mode: "history",
    scrollBehavior(to, from, savedPosition) {
        return { x: 0, y: 0 }
        // return 期望滚动到哪个的位置
    }

})
export default Router;


